import { Component, OnInit } from '@angular/core';
import { NgxHmCarouselBreakPointUp } from 'ngx-hm-carousel';
import { CrmService } from 'src/app/layout/crm/crm.service';
@Component({
  selector: 'app-chef',
  templateUrl: './chef.component.html',
  styleUrls: ['./chef.component.scss']
})
export class ChefComponent implements OnInit {
  currentIndex = 1;
  speed = 5000;
  infinite = true;
  direction = 'right';
  directionToggle = true;
  autoplay = true;
  Chefs = [];
  btnstyle = 'mat-stroked-button mat-primary';
  avatars = '1234567891234'.split('').map((x, i) => {
    const num = i;
    // const num = Math.floor(Math.random() * 1000);
    return {
      url: `https://picsum.photos/600/400/?${num}`,
      title: `${num}`
    };
  });

  breakpoint: NgxHmCarouselBreakPointUp[] = [
    {
      width: 500,
      number: 1
    },
    {
      width: 800,
      number: 2
    },
    {
      width: 1024,
      number: 4
    }
  ];

  constructor(private crmservice: CrmService) {
    this.GetAllChefs();
  }

  ngOnInit() {}

  switchIndex($event) {
    console.log($event);
  }
  // get chefs
  GetAllChefs() {
    this.crmservice.GetChefs().subscribe(Response => {
      if (Response.Status === true) {
        this.Chefs = Response.Data;
      }
    });
  }
}
